.menu-button {
    position: fixed;
    right: 1.5rem;
    bottom: 1.5rem;
    display: flex;
    flex-direction: column-reverse;

    &__menu {
        max-height: 0;
        height: 0;
        transition: all 0.2s ease-in;
        overflow: hidden;
        button {
            transform: scale(0);
            transition: transform 0.2s ease-in;
        }
    }

    &.-open {
        .menu-button__menu {
            height: auto;
            max-height: 100px;

            button {
                transform: scale(1);
            }
        }
    }
}

.menu-button__main {
    background-color: $primary-color;
    display: flex;
    align-items: center;
    height: 50px;
    width: 50px;
    border-radius: 50px;
    border: none;
    justify-content: center;
    box-shadow: 0px 0px 8px rgba(0, 0, 0, 0.281);
    color: white;
}

.menu-button__menu {
    display: flex;
    flex-direction: column;
    align-items: center;
    gap: 12px;
    margin-bottom: 12px;

    button {
        height: 35px;
        width: 35px;
        background-color: $primary-color;
        display: flex;
        align-items: center;
        border-radius: 35px;

        .material-icons-round {
            font-size: 1.3rem;
            color: white;
        }
    }
}
