.project-details-list-item {
    display: flex;
    flex-direction: column;
    justify-content: center;

    padding: 10px 0;
    background-color: white;
    border-radius: 5px;
    box-shadow: 1px 1px 5px rgba(0, 0, 0, 0.219);
    padding: 0.5rem;

    &__extra {
        height: 0;
        overflow: hidden;
    }

    .arrow {
        transition: transform 0.2s ease-in;
    }

    &.-open {
        height: auto;
        .project-details-list-item__extra {
            height: 60px;
        }
        .arrow {
            transform: rotate(180deg);
        }
    }
}

.project-details-list-item__main {
    display: flex;
    align-items: center;
    min-height: 50px;
}
.project-details-list-item__select {
    margin-right: 1rem;
    height: 0.9rem;
    width: 0.9rem;
}
.project-details-list-item__info {
    flex: 1;
    .count {
        font-size: 14px;
        margin-top: 3px;
    }
}

.project-details-list-item__extra {
    transition: height 0.2s ease-in;
    display: flex;
    align-items: center;
    justify-content: center;
    gap: 10px;
    button {
        height: 30px;
        width: 40px;
        color: white;
        background-color: $primary-color;
        border: none;
        padding: 0;
        border-radius: 5px;
        display: flex;
        align-items: center;
        justify-content: center;

        &:active,
        &:hover {
            transform: scale(0.9);
        }
    }
}

.project-details-list-item__actions {
    display: flex;
    align-items: center;
}

.project-details-list-item__counter {
    background-color: transparent;
    font-weight: bold;
    border: none;
    font-size: 1.1rem;
    text-align: center;
    width: fit-content;
    max-width: 30%;
}
