.app {
    height: 100vh;
    width: 100vw;
    overflow: auto;
    background-color: $background-color;

    nav {
        height: 50px;
        width: 100%;
        background-color: $primary-color;
        padding: 0 1rem;

        display: grid;
        grid-template-columns: 24px 1fr 24px;
        align-items: center;
        text-align: center;

        position: sticky;
        top: 0;

        .-back {
            display: flex;
            justify-content: center;
            align-items: center;
            text-decoration: none;
            color: white;
        }
    }

    h1 {
        color: white;
    }

    main {
        padding: 0 1rem;
    }
}
