.add-button {
    width: 50px;
    height: 50px;
    display: flex;
    align-items: center;
    justify-content: center;
    background-color: $primary-color;
    border: none;
    border-radius: 50px;
    color: white;
    padding: 0;
    box-shadow: 0px 0px 8px rgba(0, 0, 0, 0.281);
}
