.btn {
    background-color: transparent;
    border: none;
    border-radius: 5px;
    padding: 0;
    font-weight: bolder;
    display: flex;
    align-items: center;
    justify-content: center;
    cursor: pointer;
    &:disabled {
        background-color: #cecece;
        border: 1px solid #cecece;
    }
}

.btn-primary {
    background-color: $primary-color;
    border: 1px solid $primary-color;
    padding: 6px 10px;
    color: white;
    &:hover {
        transform: scale(0.9);
    }
}

.btn-secondary {
    border: 1px solid $primary-color;
    padding: 6px 10px;
    color: $primary-color;
    &:hover {
        transform: scale(0.9);
    }
}

.btn-danger {
    background-color: red;
    border: 1px solid red;
    padding: 6px 10px;
    color: white;
    &:hover {
        transform: scale(0.9);
    }
}
