.modal {
    position: absolute;
    height: 100vh;
    width: 100vw;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    z-index: 100;
}

.modal__background {
    width: 100%;
    height: 100%;
    background-color: rgba(0, 0, 0, 0.397);
}

.modal__body {
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    background-color: $background-color;
    width: fit-content;
    height: fit-content;
    min-height: 200px;
    min-width: 280px;
    max-width: calc(100% - 2rem);
    max-width: calc(100% - 2rem);

    border-radius: 5px;
    padding: 0.5rem 1rem;

    display: flex;
    flex-direction: column;

    header {
        display: flex;
        align-items: center;
        justify-content: space-between;
        margin: 0 -1rem;
        padding: 0 1rem;
        padding-bottom: 0.5rem;
        border-bottom: 1px solid $secondary-color;
    }
    section {
        padding: 1rem 0;
        flex: 1;
    }
    footer {
        display: flex;
        justify-content: space-between;
        align-items: center;
        margin: 0 -1rem;
        padding: 0 1rem;
        padding-top: 0.5rem;
        border-top: 1px solid $secondary-color;
    }
}
