.project-details {
}

.project-details__list {
    display: flex;
    flex-direction: column;
    gap: 1rem;
    padding-top: 1rem;
    padding-bottom: 5rem;
}

.project-details__add {
    width: 100%;
    display: flex;
    align-items: center;
    justify-content: center;

    box-shadow: 1px 1px 5px rgba(0, 0, 0, 0.219);
    font-weight: normal;

    .material-icons-round {
        font-size: 1rem;
        margin-right: 0.2rem;
    }
}

.project-details__selected {
    position: fixed;
    bottom: 1.5rem;
    gap: 10px;

    background-color: white;
    width: calc(100% - 2rem);
    border-radius: 5px;

    padding: 0.5rem;
    display: flex;
    align-items: center;
    box-shadow: 1px 1px 5px rgba(0, 0, 0, 0.219);
    z-index: 10;

    strong {
        flex: 1;
    }
}
