.projects-list {
    display: flex;
    flex-direction: column;
    width: 100%;
    gap: 1rem;
}
.project-list__info {
    display: flex;
    flex-direction: column;

    .status {
        font-size: 14px;
        margin-top: 3px;
    }
}

.projects-list__item {
    display: flex;
    justify-content: space-between;
    align-items: center;
    background-color: white;
    border-radius: 5px;
    box-shadow: 1px 1px 5px rgba(0, 0, 0, 0.219);
    padding: 0.5rem;
    width: 100%;

    &:not(:last-child) {
        padding-bottom: 0.5rem;
    }

    a {
        width: 100%;
        text-decoration: none;
        display: flex;
        color: black;
        align-items: center;
        justify-content: space-between;
        height: 40px;

        .material-icons-round {
            color: $primary-color;
        }
    }

    &:hover {
        background-color: $primary-color-light;
    }
}
