.projects {
    display: flex;
    flex-direction: column;

    .projects-list {
        padding-top: 0.5rem;
        padding-bottom: 0.5rem;
    }

    .add-button {
        position: fixed;
        bottom: 1.5rem;
        right: 1.5rem;
    }
}

.projects__header {
    height: 50px;
    background-color: $background-color;
    width: calc(100% + 2rem);
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin: 0 -1rem;
    padding: 0 1rem;
    position: sticky;
    top: 50px;

    select {
        width: 100px;
        background-color: $background-color;
        border: none;
        border-bottom: 1px solid $secondary-color;
    }
}
